<template>
	<div class="hello">
		<div v-if="loadingState">
			{{msg}}
		</div>
		<div v-else>
			<loading></loading>
		</div>
	</div>
</template>

<script>
import Loading from '@/components/Loading'
export default {
	name: "HelloWorld",
	components:{
		Loading
	},
  mounted() {
		this.$http.get('https://api.imjad.cn/hitokoto/?cat=e&charset=gbk&encode=json').then(function(res){
			this.loadingState = true
			this.msg = res.body.hitokoto
		},function(res){
			
		})
  },
  data() {
    return {
			msg: "",
			loadingState: false,
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



